
/* CSS (OurProjects.css) */
.our-projects-section {
  padding: 3vh;
  text-align: center;
}

.section-title {
  font-size: 4vw;

}

.project-types {
  margin-top: 2vh;
}

.gallery-pro {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  gap: 2vw;
  animation: fadeIn 1.5s ease-in-out;
}

.our-pro-gallery-item {
  width: 30%;
  height: 20vw;
  text-align: center;
  position: relative;
  overflow: hidden;
  transition: transform 0.3s ease;
}

.our-pro-gallery-item img {
  width: 100%;
  height: auto;
  border-radius: 1vw;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.our-pro-gallery-item:hover {
  transform: scale(1.05);
}

.our-pro-gallery-item:hover img {
  transform: scale(1.1);
  border-radius: 1vw;
  
}

.gallery-title {
  margin-top: 1.5vh;
  font-size: 1.5vw;
  color: black;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 50%;
  background: rgba(0, 0, 0, 0.4);
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.3s ease;
  padding: 1vw;
  box-sizing: border-box;

}

.gallery-item:hover .overlay {
  opacity: 1;
}

.overlay h1 {
  margin: 1vh;
  font-size: 2vh;
}
.overlay p {
  font-size: 1.5vh;
  margin-bottom: 1vh;
}

.overlay button {
  padding: 0.75vh 1vh;
  font-size: 1vw;
  color: white;
  background-color: #639651;
  border: none;
  border-radius: 0.5vh;
  cursor: pointer;

  transition: background-color 0.3s;
}

.overlay button:hover {
  background-color:#212A31 ;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@media screen and (max-width: 768px) {
  .gallery-item {
    width: 100%;
  }
  .section-title {
    font-size: clamp(1.5rem, 2vw, 2rem);
  }

}