/* CSS (ProjectPage.css) */
* h2{
    font-size: 4vw;
    margin-bottom: 1.5vh;


}
.cover-section {
    position: relative;
    height: 50vh; /* Adjust height as needed */
    background-size: cover;
    background-position: center;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .cover-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5); /* Black semi-transparent background */
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .project-name {
    font-size: 3rem;
    color: white;
    text-align: center;
    z-index: 1;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
  }

  .overview-section {
    padding: 5%;
    background-color: var(--background-color);
  }
  
  .overview-title {
   
    text-align: center;
    
  }
  
  .overview-content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
  }
  
  .overview-image-container {
    flex: 1;
    max-width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .overview-image {
    width: 100%;
    height: auto;
    border-radius: 8px;
  }
  
  .overview-text-container {
    flex: 2;
    max-width: 100%;
    font-size: clamp(0.9rem, 1.5vw, 1.2rem);
    line-height: 1.6;
    text-align: justify;
  }
  
  .overview-details-container {
    flex: 1;
    max-width: 30%;
    padding: 1.5rem;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    text-align: left;
    color: white;
  }

  
  .overview-details-container ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .overview-details-container li {
    margin-bottom: 0.5rem;
    font-size: clamp(0.9rem, 1.5vw, 1.2rem);
  }
  
  .overview-details-container li strong {
    font-weight: bold;
  }
  
  @media screen and (max-width: 768px) {
    .overview-content {
      flex-direction: column;
    }
  
    .overview-image-container,
    .overview-text-container,
    .overview-details-container {
      max-width: 100%;
    }
  
    .overview-title {
      font-size: clamp(1.2rem, 4vw, 2rem);
    }
  }
  


  .amenities-section {
    text-align: center;
    padding: 2% 5%;
  }
 
  
  .amenities-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
    gap: 2rem;
    justify-items: center;
    align-items: center;
  }
  
  .amenity-item {
    text-align: center;
  }
  
  .amenity-logo {
    width: clamp(40px, 5vw, 60px);
    height: clamp(40px, 5vw, 60px);
    margin-bottom: 1rem;
  }
  
  .amenity-item p {
    font-size: clamp(0.9rem, 1.5vw, 1.2rem);
  }

  .location-section {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  
  
  .location-content {
    display: flex;
    flex-direction: row;
    width: 100%;
    max-width: 1200px;
    color: black;
    overflow: hidden;
  }
  
  .map-container {
    flex: 1;
    width: 50%;
  }
  
  .location-map {
    width: 100%;
    height: 100%;
    border: none;
  }
  
  .location-details {
    flex: 1;
    width: 50%;
    padding: 2rem;
    text-align: left;
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: white;
  }
  
  .location-details h3 {
    font-size: clamp(1.2rem, 2vw, 1.8rem);
    margin-bottom: 1rem;
  }
  
  .location-details p {
    font-size: clamp(1rem, 1.5vw, 1.2rem);
    margin-bottom: 1.5rem;
  }
  
  .location-details ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .location-details li {
    font-size: clamp(1rem, 1.5vw, 1.2rem);
    margin-bottom: 0.5rem;
  }
  
  .location-details li strong {
    font-weight: bold;
  }
  
  @media screen and (max-width: 768px) {
    .location-content {
      flex-direction: column;
    }
  
    .map-container,
    .location-details {
      width: 100%;
    }
  
    .location-map {
      height: 300px;
    }
  }

.layout-plans-section {
  padding: 3rem 1rem;
  text-align: center;
}

.section-title {
  font-size: clamp(1.5rem, 3vw, 2.5rem);
  margin-bottom: 2rem;
}

.plans-gallery {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1.5rem;
  justify-items: center;
}

.plan-item {
  width: 100%;
  position: relative;
  cursor: pointer;
  transform: translateY(50px);
  opacity: 0;
  animation: fadeInUp 0.8s ease forwards;
  animation-delay: calc(var(--animation-index) * 0.1s);
}

.plan-item img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  z-index: 1;
}

.plan-item:hover .plan-overlay {
  opacity: 1;
}

.plan-item:hover {
  transform: scale(1.05);
}



.plan-text {
  margin-top: 0.5rem;
  font-size: clamp(0.9rem, 1.5vw, 1.2rem);
  color: var(--text-color);
  z-index: 3;
}

@keyframes fadeInUp {
  0% {
    transform: translateY(50px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

/* Full-Screen Preview */
.plan-preview-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.plan-preview {
  position: relative;
  max-width: 80%;
  max-height: 80%;
}

.plan-preview img {
  width: 100%;
  height: auto;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

.close-preview {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  font-size: 2rem;
  color: white;
  cursor: pointer;
}



.gallery-section {

  background-color: var(--background-color);
  text-align: center;
}

.gallery-container {
  width: 100%;
  display: flex;
  gap: 1rem;
  overflow-x: auto;
  scroll-behavior: smooth;
  padding-bottom: 1rem;
}

.gallery-item {
  flex: 0 0 auto;
  width: clamp(200px, 50vw, 300px); /* Dynamically adjusts width */
  height: clamp(150px, 30vw, 200px); /* Dynamically adjusts height */
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.gallery-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.gallery-item:hover {
  transform: scale(1.05);
}

/* Horizontal scroll styling */
.gallery-container::-webkit-scrollbar {
  height: 8px;
}

.gallery-container::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 4px;
}

.gallery-container::-webkit-scrollbar-track {
  background-color: rgba(0, 0, 0, 0.1);
}

@media screen and (max-width: 768px) {
  .gallery-item {
    width: clamp(150px, 40vw, 250px); /* Adjust for smaller devices */
    height: clamp(100px, 30vw, 150px);
  }

  .section-title {
    font-size: clamp(1.2rem, 4vw, 2rem);
  }
}


.video-section {
  width: 100%;
  padding: 3rem 1rem;
  text-align: center;
}

.section-title {
  font-size: clamp(1.5rem, 3vw, 2.5rem); /* Ensure responsiveness */
  margin-bottom: 2rem; /* Adds space between the title and the video */
  color: var(--text-color);
  text-align: center;
}

.video-container {
  width: 100%;
  max-width: 90%;
  margin: 0 auto;
  aspect-ratio: 16 / 9; /* Maintains a responsive 16:9 ratio */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  overflow: hidden;
}

.video-container iframe {
  width: 100%;
  height: 100%;
  border: none;
}

@media screen and (min-width: 768px) {
  .video-container {
    max-width: 800px; /* Wider videos on larger screens */
  }
}

@media screen and (max-width: 480px) {
  .section-title {
    font-size: clamp(1.2rem, 4vw, 2rem); /* Smaller titles for small screens */
  }

  .video-container {
    max-width: 100%;
    border-radius: 5px; /* Slightly reduced radius for smaller screens */
  }
}


.download-brochure-section {
  text-align: center;
  margin: 2rem 0;
}

.download-button {
  display: inline-block;
  padding: 0.8rem 2rem;
  font-size: clamp(1rem, 1.5vw, 1.2rem);
  font-weight: bold;
  text-decoration: none;
  color: black;
  border: 2px solid #b22222; /* Adjust the border color */
  border-radius: 50px; /* Fully rounded corners */
  transition: all 0.3s ease;
}

.download-button:hover {
  background-color: #b22222; /* Same as border color */
  color: white;
}