.home-page-intro {
    display: flex;
  

  }

.intro-video-section {
    width: 70%;
    display: flex;
    align-items: flex-start;
    justify-content: center;
  }


.intro-video {
    width: 100%;
    height: auto;
  }


.text-section {
    width: 30%;
    padding: 2vh;
    display: flex;
    flex-direction: column;

    justify-content: center;
    overflow-y: auto;
  max-height: 100%;
  box-sizing: border-box;
  
  }


.logo-intro img {
    width: 20vw;
    height: auto;
    margin-bottom: 2vh;
  }


.introduction p {
    font-size: 0.80vw;
    color: #666;
    padding: 0.5vw;
    line-height: 1.5;
    word-wrap: break-word;
  word-break: break-word;
  max-width: 100%;
  overflow-wrap: break-word;
  text-align: center;

  }

  .intro-btn {
    text-align: center;
    margin-top: 2vh;
    padding: 1vw 2vh;
    font-size: 1vw;
    color: #fff;
    background-color: #2E3944;
    opacity: 50%;
    border: none;
    border-radius: 1vh;
    cursor: pointer;
    transition: transform 0.3s ease, background-color 0.3s ease;
  }
  .intro-btn:hover {
    background-color: #639651;
  }
  

  @media screen and (max-width: 700px) {
    .home-page-intro {
      flex-direction: column;
      
    }
    .video-section {
      width: 100%;
      height: auto;
    }
    .text-section {
      
      width: 100%;
    height: 50%;
    
    overflow-y: auto;}

    .introduction{
        max-width: 100%;
    }

    .introduction p{
      font-size: 0.80vh;
    }

    .logo-intro img {
      width: 15vh;
      height: auto;
      margin-bottom: 2vh;
    }

    .intro-btn{
      font-size: 0.8vh;
    }
    
    }
  
  /* Animation for the button pop */
.pop .intro-btn {
  animation: popAnimation 0.6s ease-in-out;
}

@keyframes popAnimation {
  0% {
    transform: scale(0.8);
    opacity: 0.5;
  }
  50% {
    transform: scale(1.2);
    opacity: 1;
  }
  100% {
    transform: scale(1);
  }
}